textarea {
  min-height: 100px;
  min-width: 250px;
  display: block;
}
select {
  background: transparent url("data:image/png;base64,R0lGODlhDwAUAIABAAAAAP///yH5BAEAAAEALAAAAAAPABQAAAIXjI+py+0Po5wH2HsXzmw//lHiSJZmUAAAOw==") no-repeat 95% 49%;
}
img {
  margin: 0;
}
img.thumbnail {
    height: auto;
    padding: 4px;
    line-height: 1.42;
    border: 1px solid #efefef;
    background-color: #FFF;
    border-radius: 2px;
}
img.round {
    border-radius: 10%;
}
img.circle {
    border-radius: 50%;
}
.progress {
  display: block;
  width: 100%;
  height: 25px;
  float: left;
  background-color: #efefef;
  overflow: hidden;
  border-radius: 2px;
  margin-bottom: 10px;
  box-shadow: inset 0 0.1em 0.1em rgba(0, 0, 0, 0.1);
}
.progress.round {
    border-radius: 12px;
}
.progress .bar {
    display: inline-block;
    float: left;
    min-width: 2em;
    height: 25px;
    text-align: center;
    background-color: #3F3F3F;
    color: #FFF;
}
.progress .bar span {
      position: relative;
      top: 2.5px;
}
.progress .bar.primary {
      background-color: #0174DF;
}
.progress .bar.success {
      background-color: #52C652;
}
.progress .bar.error {
      background-color: #E93F3C;
}
.progress .bar.warn {
      background-color: #F5AD1E;
}
.progress .bar.info {
      background-color: #52B7D6;
}
.progress .bar.dark {
      background-color: #3F3F3F;
}
tag,
.tag {
  padding: 4px 12px;
  font-size: 12px;
  font-weight: 500;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  background-color: #EEE;
  color: #3F3F3F;
  border-radius: 50px;
  display: -moz-inline-stack;
  display: inline-block;
  *zoom: 1;
  *display: inline;
}
tag.square,
  .tag.square {
    border-radius: 2px;
}
tag.primary,
  .tag.primary {
    color: #FFF;
    background: #0174DF;
}
tag.success,
  .tag.success {
    color: #FFF;
    background: #52C652;
}
tag.error,
  .tag.error {
    color: #FFF;
    background: #E93F3C;
}
tag.warn,
  .tag.warn {
    color: #FFF;
    background: #F5AD1E;
}
tag.info,
  .tag.info {
    color: #FFF;
    background: #52B7D6;
}
tag.dark,
  .tag.dark {
    color: #FFF;
    background: #3F3F3F;
}
main, .container {
  max-width: 1100px;
}
main {
  margin-bottom: 5em;
}
.hero {
  text-align: center;
  background: #c69c6d;
  background: linear-gradient(25deg, #c69c6d 0%, #a67c52 100%);
  background: -webkit-linear-gradient(25deg, #c69c6d 0%, #a67c52 100%);
}
.hero pre {
    margin: 0 auto;
    max-width: 300px;
    padding: 12px 24px;
}
.cellar {
  padding: 3em 0 2em;
  text-align: center;
  background: #fafafa;
  border-bottom: 2px solid rgba(0, 0, 0, 0.1);
}
h5 {
  margin-bottom: 1em;
}
@media (max-width: 800px) {
.full {
    padding-bottom: 2em;
}
}



